import React from 'react';
import "./style.css"
import { fetchToDoList, updateToDoList, isLogin, getRequestToDoList } from './backend.js';
import { SideNav, Navbar } from "./reused-components/navigation.js";
import { Redirect } from "react-router-dom";
import { ToDoDraggable } from "./ToDo.js";
import "./reused-components/navigation.css";






export class WorkspacePage extends React.Component {
  constructor(props) {
    super(props)
    getRequestToDoList()
    this.state = { isExtended: "extended", toDoList: fetchToDoList(), }
    window.addEventListener("resize", this.updateSize)
  }
  changeState = () => {
    if (this.state.isExtended === "extended") {
      this.setState((props, state) => {
        return { isExtended: "not-extended", toDoList: this.state.toDoList }
      })
    }
    else {
      this.setState((props, state) => {
        return { isExtended: "extended", toDoList: this.state.toDoList, }
      })
    }
  }

  updateToDo = (list, index) => {
    var temp_list = this.state.toDoList;
    temp_list[index] = list;
    updateToDoList(temp_list);
    this.setState((props, state) => {
      return { isExtended: this.state.isExtended, toDoList: temp_list, }
    })
  }


  addToToDo = (item, index) => {
    if (index === this.state.toDoList.length) {
      return;
    }
    var temp_list = this.state.toDoList;
    temp_list[index].push(item);
    updateToDoList(temp_list);
    this.setState((props, state) => {
      return { isExtended: this.state.isExtended, toDoList: temp_list, }
    })
  }
  render() {
    getRequestToDoList()
    return (
      <div>
        <div style={{ position: "absolute" }}><Navbar pageName="Your Workspace" sideNavAnimation={this.changeState} /></div>
        <div style={{ position: "absolute", zIndex: -1 }} className={this.state.isExtended}>
          <div style={{ display: "inline-block", position: "absolute" }}><SideNav isExtended={"extended"} /></div>
          <div className="todo-outer-div" style={((this.state.isExtended === "extended") ? {} : {width: "100vw"})}>
            <div style={{ width: "1200px" }}>
              <ToDoDraggable />
            </div>
          </div>
        </div>


      </div>
    )
  }
}





