class ToDoEditPopUp extends React.Component {
  constructor(props) {
    super(props);
    this.state = {actionSteps: this.props.state[this.props.indexes[0]][this.props.indexes[1]].actionSteps}
  }
  editToDoProperty = (propertyName, value) => {
    var newState = this.props.state;
    newState[this.props.indexes[0]][this.props.indexes[1]][propertyName] = value;
    this.props.updateToDo(newState)
    if (propertyName === "actionSteps") {
      this.setState({actionSteps: value})
    }

  }
  deleteItem = () => {
    var newState = this.props.state;
    newState[this.props.indexes[0]] = removeByIndex(newState[this.props.indexes[0]], this.props.indexes[1])
    this.props.updateToDo(newState)
    this.props.changePopUp()
  }
  render() {
    return (<div style={{ minWidth: "25vw", backgroundColor: "white", opacity: "100%", zIndex: 2, borderRadius: "20px", paddingBottom: "50px", 
    paddingLeft: "20px", paddingRight: "20px", paddingTop: "5px"}}>
      <button style={{ cursor: "pointer", float: "right", marginTop: "15px", marginRight: "15px" }} onClick={this.props.changePopUp}>
        <CloseIcon /></button>
        <h1 style={{ marginLeft: "20px" }}>{this.props.item.content}   
        <div style={{display: "inline-block", cursor: "pointer"}} onClick={this.deleteItem}>
          <TrashIcon />
        </div>
        </h1>
        <div style={{display: "inline-block"}}>
          Assign user <input style={{width: "70px", padding: "5px", height: "40px"}} placeholder="user" onChange={(text) => this.editToDoProperty("assignedUser", text.target.value)}/>
          </div>
        <div style={{display: "inline-block", marginLeft: "20px"}}>
          Difficulty <input style={{width: "70px", padding: "5px", height: "40px"}} placeholder="difficulty" onChange={(text) => this.editToDoProperty("difficulty", text.target.value)}/>
          </div>
          
          <div style={{display: "inline-block", marginLeft: "20px"}}>
          Duration <input style={{width: "50px", padding: "5px", height: "40px"}} placeholder="hours" onChange={(text) => this.editToDoProperty("hours", text.target.value)}/>
          </div>
          <div style={{display: "block", marginLeft: "20px"}}>
          Due Date <input style={{width: "50px", padding: "5px", height: "40px"}} placeholder="date" onChange={(text) => this.editToDoProperty("dueDate", text.target.value)}/>
          </div>
          <div key={JSON.stringify(this.state.actionSteps)}>
            <h1>Action Steps</h1>

            <ActionStepsDraggable state={this.state.actionSteps} editToDoProperty={this.editToDoProperty}/>
          </div>
          <ActionStepAddButton item={this.props.state[this.props.indexes[0]][this.props.indexes[1]]} editToDoProperty={this.editToDoProperty}/>

    </div>
    )
  }
}


