function makeActionStepsList(list) {
  var tempList = list.map((item, index) => ({
    content: item[0],
    id: `actionSteps ID ${index}`,
    isComplete: item[1]
  }))
  return tempList
}

function makeRegularActionsStepsList(list) {
  return list.map(item => [item.content, item.isComplete])
}
function ActionStepsDraggable(props) {
  const [state, setState] = useState([makeActionStepsList(props.state)]);

  function onDragEnd(result) {
    const { source, destination } = result;

    // dropped outside the list
    if (!destination) {
      return;
    }
    const sInd = +source.droppableId;
    const dInd = +destination.droppableId;

    if (sInd === dInd) {
      const items = reorder(state[sInd], source.index, destination.index);
      const newState = [...state];
      newState[sInd] = items;
      props.editToDoProperty("actionSteps",makeRegularActionsStepsList(newState[0]))
      setState(newState);
    }
  }
  return (

    <div style={{position: "relative"}}>
      <div style={{ display: "flex" }}>
        <DragDropContext onDragEnd={onDragEnd}>
          
          {state.map((el, ind) =>{ 
            return (<div style={{marginLeft: "50px", userSelect: "none"}}>

              <div>
            <Droppable key={ind} droppableId={`${ind}`}>
              {(provided, snapshot) => (
                <div
                  ref={provided.innerRef}
                  style={getListStyle(snapshot.isDraggingOver)}
                  {...provided.droppableProps}
                >
                  <div style={{ position: "relative", maxHeight: "340px", width: "230px" }} className="scrollingThing">
                  {el.map((item, index) => {
                    return(
                    <Draggable
                      key={item.id}
                      draggableId={item.id}
                      index={index}
                    >
                      {(provided, snapshot) => (
                        <div
                          ref={provided.innerRef}
                          {...provided.draggableProps}
                          {...provided.dragHandleProps}
                          style={getItemStyle(
                            snapshot.isDragging,
                            provided.draggableProps.style
                          )}
                        > 
                        <input style={{height: "10px", width: "10px"}} type="checkbox"
                        defaultChecked={item.isComplete}
                        onChange={(event) => {
                          var tempActionSteps = props.state;
                          tempActionSteps[index][1] = !tempActionSteps[index][1]
                          props.editToDoProperty("actionSteps", tempActionSteps )}}
                        />{item.content} <button style={{cursor: "pointer"}} onClick={() => {
                          var tempActionSteps = removeByIndex(props.state, index);
                          props.editToDoProperty("actionSteps", tempActionSteps )}}><TrashIcon /></button>
                        </div>
                      )}
                    </Draggable>
                  )})}
                  </div>
                  {provided.placeholder}
                </div>
              )}
              
            </Droppable></div>

            </div>
          )})}
        </DragDropContext>
      </div>
    </div>
  );
}