import axios from "axios";
export var user = {
    username: "",
    toDoList: [[],[],[],[]],
    cash: 0,
    xp: 0,
}

function makeToDoList(lst, offset = 0) {
    return (lst.map((content, index) => {
        content = content.split("//>")
        return ({
        id: `${index} ${offset}`,
        content: content[0],
        assignedUser: content[1],
        difficulty: content[2],
        hours: content[3],
        dueDate: content[4],
        actionSteps: JSON.parse(content[5])
        //assignedUser: "John" //Doesnt work because of setState not updatig in ToDo
    })}
    ))
}
export async function getRequestToDoList() {
    
    var todo_list;
    await axios.get(`https://cambio-api.herokuapp.com/api/Groups/${localStorage.getItem("group")}/`).then(
        response => {
            todo_list = [makeToDoList(response.data.backlog),
            makeToDoList(response.data.to_do, 1000), makeToDoList(response.data.doing, 2000),
           makeToDoList(response.data.done,3000)];
            //user.cash = response.data.cash;
            //user.xp = response.data.xp;
        }
    )
    //user.toDoList = todo_list
    return todo_list

}
var downloadData
export async function loadDownloadData() {
    var downloadData1;
        await axios.get("https://cambio-api.herokuapp.com/api/downloads/1/").then(
            response => {downloadData1 = response.data
                downloadData = response.data}
        )
    return downloadData1;
}

export function fetchChallenges() {
    return downloadData.challenges
} 

export function fetchToDoList() {
    //returns a list with 4 lists that contain strings
    //return [[],[],[],[]];
    return user.toDoList

    
}

export function updateToDoList(list) {
    user.toDoList = list;
    var tempList = [];
    list.forEach(lst => {
        var tempLst = [];
        lst.forEach(item => {
            tempLst.push(item.content + "//>" + item.assignedUser + "//>" + item.difficulty + "//>" + item.hours + "//>"+ item.dueDate + "//>" + JSON.stringify(item.actionSteps))
        })
        tempList.push(tempLst)
    })
    
    axios.patch(`https://cambio-api.herokuapp.com/api/Groups/${localStorage.getItem("group")}/`, ({
        backlog: tempList[0],
        to_do: tempList[1],
        doing: tempList[2],
        done: tempList[3],
    }))
    //returns nothing
}

export function isLogin() {
    return JSON.parse(localStorage.getItem("loggedIn"));
}
export async function login(username, password) {
    const config = {
        headers: {
          'Content-Type': 'application/json',
        },
      };
    await axios.post("https://cambio-api.herokuapp.com/api/auth/login", {username: username, password: password}, config).then(
      async res => {
        localStorage.setItem("loggedIn",res.data.token!==null)
        localStorage.setItem("token", res.data.token)
        localStorage.setItem("user", JSON.stringify(res.data))
        console.log(res.data)
        await axios.get('https://cambio-api.herokuapp.com/api/Students/', {
        headers: {
        'Authorization': `token ${localStorage.getItem("token")}`
        }
        })
        .then((response) => {
        console.log(response.data)
        localStorage.setItem("group", response.data[0].group_name)
        user.cash = response.data[0].cash
        user.xp = response.data[0].xp
        })
        .catch((error) => {
        console.error(error)
        })
        return true
      }
    )
    return false;

}

