var errorMessage = null

class LoginWidget extends React.Component {


    constructor(props) {
        super(props);
        this.state = {username: "Username", password: "Password", showPassword: "password", errorMessage: errorMessage};
    }
    updateUsername = (text) => {
        
        errorMessage = null
        this.setState((props, state) => {
            return {username: text, password: this.state.password, errorMessage: null}
        })
    }
    updatePassword = (text) => {
       
        this.setState((props, state) => {
            return {username: this.state.username, password: text, errorMessage: null}
        })
    }
    handleShowPassword = () => {
        errorMessage = null
        var showPassword = "text";
        if (this.state.showPassword === "text") {
            showPassword = "password";
        }
        this.setState((props, state)=> {
            return {username: this.state.username, password: this.state.password, showPassword: showPassword}
        })
    }
    renderLoginButton() {
            return <button style={{cursor: "pointer"}} onClick={() => {errorMessage= "Wrong password or username";}}><Link to="/" >
                <svg xmlns="http://www.w3.org/2000/svg" width="80" height="36" viewBox="0 0 160 66">
  <rect id="Rectangle_1585" data-name="Rectangle 1585" width="160" height="66" rx="19" fill="#8b4389"/>
  <text id="Login" transform="translate(30 50)" fill="#fff" font-size="40" font-family="SegoeUI-Semibold, Segoe UI" font-weight="600"><tspan x="0" y="0">Login</tspan></text>
</svg>
</Link></button>;
             
    }
    renderShowPassword = ()=> {
        if (this.state.showPassword === "password"){
            return "show password";
        }
        else {
            return "hide password";
        }
    }
    render() {
        login(this.state.username, this.state.password)
        return (<div style={{ padding: "50px", borderRadius: "20px"}}>
            <h1 style={{marginLeft: "40px"}}>Welcome</h1>
            
            <input style={{width: "300px"}} onChange={(text) => {this.updateUsername(text.target.value)}} placeholder="Username"></input>
            <div>
            <input style={{width: "300px"}} onChange={(text) => {this.updatePassword(text.target.value)}} type={this.state.showPassword} placeholder="Password"></input></div>
            <div style={{transform: "translateY(10px)"}}>
            <p1 style={{color: "red"}}>{this.state.errorMessage}</p1>
            <button onClick={this.handleShowPassword} style={{cursor: "pointer"}}><p1>{this.renderShowPassword()}</p1></button>
            <div style={{float: "right"}}>{this.renderLoginButton()}</div>
            </div>
        </div>);
    }
}

export class LoginPage extends React.Component {
    constructor(props) {
        super(props);
        getRequestToDoList()
    }
    render() {
        return (<div><div style={{height: "100%", overflow: "hidden", margin: "0"}}>
            
        <div style={{position: "absolute", 
        bottom: "-10px",
        }}>
            <LoginSideSvg />
            
            </div>
            
        </div>
        <div style={{ marginLeft: "100px", transform: "translateY(100px)"}}>
            <img src={logo} style={{height: "40vh"}} />
            </div>
            <div style={{width: "400px", right: "100px", position: "absolute", bottom: "30vh"}}>
                <div style={{}}><LoginWidget /></div>
            </div>
        </div>
        )
    }
}